.contact{
  color: $color-main;
  width: 100%;
  height: 100vh;
  position: relative;
  &:after{
    content: "";
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    z-index: -1;
    background: url("../../assets/Background-Contact-Form.jpg");
    opacity: 0.5;
  }
  .contact__content {
    margin-left: 60vw;
    width: 30%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    h2{
      font-size: calc(10px + 3vmin);
    }
    .succes{
      color: green;
      margin-bottom: 2vh;
    }
    h3{
      font-size: calc(8px + 1vmin);
    }
    .decoration {
      margin: 3vh auto;
      background-image: url("../../assets/Decoration.svg");
      height: 33px;
      width: 253px;
    }
    input,
    textarea {
      margin: 1vh auto;
      width: 100%;
      background: transparent;
      border: none;
      outline: none;
      border-bottom: 1px solid #707070;
      overflow: hidden;
      &:focus {
        border-bottom: 1px solid black;
      }
    }
    ::-webkit-input-placeholder { /* Chrome */
      color: #3C3C3C26;
    }
    :-ms-input-placeholder { /* IE 10+ */
      color: #3C3C3C26;
    }
    ::-moz-placeholder { /* Firefox 19+ */
      color: #3C3C3C26;
    }
    :-moz-placeholder { /* Firefox 4 - 18 */
      color: #3C3C3C26;
    }
    .name {
      width: 50%;
      float: left;
      padding-right: 1vw;
      &:after{
        clear: both;
      }
    }
    .email {
      width: 50%;
      float: right;
      padding-left: 1vw;
      &:after{
        clear: both;
      }
    }
    input[type=submit] {
      margin-left: 70%;
      width: 30%;
      margin-top: 1vh;
      border: 1px solid $color-main;
      padding: 1vh 2vh;
      font-weight: 300;
      cursor: pointer;
    }
    form p{
      font-size: calc(4px + 0.5vmin);
      color:red
    }
  }
  footer{
    width: 100%;
    text-align: center;
    position: absolute;
    bottom: 0;
    margin-bottom: 2vh;
    .icon{
      display: inline-block;
      height: 4vmin;
      width: 4vmin;
      position: absolute;
      bottom: 0;
    }
    .facebook{
      right: 9vw;
      background: url('../../assets/Facebook.svg') 0% 0% no-repeat padding-box;
      background-size: contain;
    }
    .instagram{
      right: 5vw;
      background: url('../../assets/Instagram.svg') 0% 0% no-repeat padding-box;
      background-size: contain;
    }
  }
}